import "@babel/polyfill";

import React from 'react'
import App, { Container } from 'next/app'
import { colors, sizes } from '../config'

if (process.env.NODE_ENV == 'development' && typeof window != 'undefined') {
    require('console-panel/src/console-panel.js')
    require('console-panel/src/console-panel.css')

    consolePanel.enable()
}

export default class MyApp extends App {
    static async getInitialProps({ Component, router, ctx }) {
        let pageProps = {}

        if (Component.getInitialProps) {
            pageProps = await Component.getInitialProps(ctx)
        }

        return { pageProps }
    }

    render () {
        const { Component, pageProps } = this.props

        return (
            <Container>
                <Component {...pageProps} />
                <style global jsx>{/* syntax:css */`
                    @font-face {
                        font-family: 'Gotham-Black';
                        src: url('/static/gothamblack-webfont.eot');
                        src: url('/static/gothamblack-webfont.eot?#iefix') format('embedded-opentype'),
                             url('/static/gothamblack-webfont.woff2') format('woff2'),
                             url('/static/gothamblack-webfont.woff') format('woff'),
                             url('/static/gothamblack-webfont.ttf') format('truetype');
                        font-weight: normal;
                        font-style: normal;
                    }

                    html, body {
                        margin: 0;
                        padding: 0;
                        color: ${colors.generalFont};
                        user-select: none;
                    }

                    html {
                        background: #333;
                    }

                    body {
                        width: ${sizes.width - sizes.padding * 2}px;
                        height: ${sizes.height - sizes.padding * 2}px;
                        background-image: url("/static/background.png");
                        background-repeat: no-repeat;
                        background-color: #252524;
                        background-size: 100%;
                        padding: ${sizes.padding}px;
                    }
                `}</style>
            </Container>
        )
    }
}
